import NotificationsModel from '../models/notifications';

export default async function markNotificationAsRead(ids) {
  const Notifications = NotificationsModel();

  await Notifications.findByIdAndUpdate(
    {
      _id: { $in: ids },
    },
    { $set: { new: false } }
  );
  return { message: 'Notifications marked as read successfully' };
}
